<template>
  <div>
    <el-page-header content="创建博客" icon="" title="博客管理
  " />
    <el-form ref="newsFormRef" :model="newsForm" :rules="newsFormRules" label-width="80px" class="demo-ruleForm" status-icon>
      <el-form-item label="标题" prop="title">
        <el-input v-model="newsForm.title" />
      </el-form-item>
      <el-form-item label="内容" prop="contant">
        <!-- 富文本组件 -->
        <editor @event="handleContant"></editor>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitNews()">添加博客</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
  
  <script setup>
import { ref, reactive, onMounted } from 'vue'
import axios from 'axios'
import editor from '@/components/Editor.vue'
import { ElMessage } from 'element-plus'
import { useRouter } from 'vue-router'
const router = useRouter()
let blogList = ref([])
let arr = ref([])
// 表单对象
const newsFormRef = ref()
// 表单元素
const newsForm = reactive({
  title: '',
  contant: ''
})

// 校验规则
const newsFormRules = reactive({
  title: [{ required: true, message: '请输入标题', trigger: 'blur' }],
  contant: [{ required: true, message: '请输入内容', trigger: 'blur' }]
})

// 测试
onMounted(async () => {
  if (localStorage.getItem('blogList')) {
    blogList.value = await JSON.parse(localStorage.getItem('blogList'))
    console.log(blogList.value)
  }
})

// 获得editor组件内容
const handleContant = data => {
  // console.log(data)
  newsForm.contant = data
}
// 格式化时间
const formattedTime = () => {
  const date = new Date()
  const year = date.getFullYear()
  const month = (date.getMonth() + 1).toString().padStart(2, '0')
  const day = date.getDate().toString().padStart(2, '0')

  return `${year}-${month}-${day}`
}

const submitNews = () => {
  // UserFormRef.value.validate校验表单的数据，参数valid为bool值
  newsFormRef.value.validate(async valid => {
    if (valid) {
      const newsData = reactive({
        time: formattedTime(),
        title: newsForm.title,
        contant: newsForm.contant
      })
      blogList.value.unshift(newsData)
      localStorage.setItem('blogList', JSON.stringify(blogList.value))

      ElMessage.success('添加成功')
    }
  })
}
</script>
  
  <style lang="scss" scoped>
.el-table {
  margin-top: 50px;
}
</style>